import { Component, OnInit } from '@angular/core';
import { QueryFn } from '@angular/fire/firestore';

import { ArticlesService } from '../services/articles/articles.service';

@Component({
  selector: 'app-tag',
  templateUrl: './tag.component.html',
  styleUrls: ['./tag.component.scss']
})
export class TagComponent implements OnInit {
  public list: API.Tag[];
  public articleList: API.Article[];
  public currentTag: API.Tag;

  constructor(
    private articlesService: ArticlesService
  ) { }

  ngOnInit() {
    this.articlesService.getTags().subscribe(data => {
      const tagMap: Map<string, API.Tag> = new Map();

      for (const { value } of data) {
        if (!tagMap.has(value)) tagMap.set(value, <API.Category>{ id: value, title: value, total: 0 });
        tagMap.get(value).total++;
      }

      const list: API.Tag[] = [];
      const totalList: number[] = [];

      tagMap.forEach(value => (list.push(value), totalList.push(value.total)));

      // 构建分级对象: first, second, third (用以显示不同级类型样式)
      const levelMap = this.getLevelNum(totalList);

      this.list = list.map(item => ({
        ...item,
        level: item.total > levelMap.first ? 'level1' : item.total > levelMap.second ? 'level2' : item.total > levelMap.third ? 'level3' : ''
      }));
    });
  }

  /**
   * Get Archive List
   * @param {QueryFn} queryList
   */
  getArchiveList(tagName: string) {
    let queryFn: QueryFn;

    if (tagName) {
      queryFn = ref => ref.where('tags', 'array-contains', tagName).orderBy('createTime', 'desc');
    }

    this.articlesService.getArticleList(queryFn).subscribe((data: Array<API.Article>) => {
      this.articleList = data;
    });
  }

  // Click category
  handleClick(tag: API.Tag) {
    this.currentTag = tag;

    this.getArchiveList(tag.title);
  }

  /**
   * 获取分级数据, 大于对应数字的数为当前级
   * @param {number[]} list
   * @returns { first: number, second: number, third: number }
   */
  getLevelNum(list: number[]): { first: number, second: number, third: number } {
    list.sort((a, b) => b - a);

    let first: number;
    let second: number;
    let third: number;

    // List 长度小于 3 时, 不取前三级数据
    if (list.length < 3) {
      first = second = third = Number.POSITIVE_INFINITY;
    } else if (list.length < 6) {
      first = Number.POSITIVE_INFINITY;
      // 9, 8, 8, 8, 7
      // second: 最大的一个数, second 取第 2 个值
      second = list[1];
      // third: 取除了 second 外的最大的两个值
      if (second < list[0]) list.shift();

      third = list[2];
    } else if (list.length < 11) {
      first = list[1];
      if (second < list[0]) list.shift();

      second = list[2];
      while (list.length) {
        if (list[0] <= second) break;
        list.shift();
      }
      third = list[3];
    } else {
      first = list[2];
      while (list.length) {
        if (list[0] <= first) break;
        list.shift();
      }
      second = list[3];
      while (list.length) {
        if (list[0] <= second) break;
        list.shift();
      }
      third = list[4];
    }

    return { first, second, third };
  }
}
