import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { HomeComponent } from './home/home.component';
import { ArticlesComponent } from './articles/articles.component';
import { NotFoundComponent } from './component/not-found/not-found.component';
import { ArchivesComponent } from './archives/archives.component';
import { CategoryComponent } from './category/category.component';
import { TagComponent } from './tag/tag.component';
import { LoginComponent } from './login/login.component';
import { UpdateArticleComponent } from './update-article/update-article.component';
import { CorrectionDataComponent } from './correction-data/correction-data.component';
import { AboutComponent } from './about/about.component';

const routes: Routes = [
  { path: '', redirectTo: '/home', pathMatch: 'full' },
  { path: 'home', component: HomeComponent, data: { title: 'Home' } },
  { path: 'articles/:id', component: ArticlesComponent },
  { path: 'archives', component: ArchivesComponent },
  { path: 'archives/:date', component: ArchivesComponent },
  { path: 'category', component: CategoryComponent },
  { path: 'tag', component: TagComponent },
  // 登录
  { path: 'login', component: LoginComponent },
  // 更新文章(上传, 更新)
  { path: 'update-article', component: UpdateArticleComponent },
  { path: 'update-article/:id', component: UpdateArticleComponent },
  // 数据订正
  { path: 'correction-data', component: CorrectionDataComponent },
  { path: 'about', component: AboutComponent },
  { path: '**', component: NotFoundComponent }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
