import { Injectable, EventEmitter } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { tap, catchError } from 'rxjs/operators';
import { Observable, of } from 'rxjs';

import * as commonmark from 'commonmark';

interface Head {
  level: number;
  content: string;
  anchor: string;
}

@Injectable({
  providedIn: 'root'
})
export class CommonMarkService {
  public headList: Array<Head> = [];
  public eventEmit: EventEmitter<any>;

  constructor(
    private http: HttpClient
  ) {
    this.eventEmit = new EventEmitter();
  }

  /**
   * get head by list
   */
  getHeadList() {
    return this.headList;
  }

  /**
   * get markdown source by src.
   * @param {string} src resource link
   * @return {string} recource responce
   */
  getSource(src: string) {
    return this.http.get(src).pipe(
      tap(_ => this.log(`fetch markdown content: ${_}`)),
      catchError(this.handleError(`get markdown content`))
    );
  }

  /**
   * compile markdown to html
   * @param {string} markdown markdown content.
   * @return {string} html string.
   */
  compile(markdown: string): string {
    const reader = new commonmark.Parser();
    const writer = new commonmark.HtmlRenderer({ sourcepos: true, headAnchor: true });
    this.headList = [];

    writer.attrs = function (node) {
      var att = [];
      if (this.options.sourcepos) {
        var pos = node.sourcepos;
        if (pos) {
          att.push(['data-sourcepos', String(pos[0][0]) + ':' +
            String(pos[0][1]) + '-' + String(pos[1][0]) + ':' +
            String(pos[1][1])]);
        }
      }

      if (this.options.headAnchor && node.type === 'heading') {
        var pos = node.sourcepos;
        if (pos) {
          att.push(['id', String(pos[0][0]) + ':' +
            String(pos[0][1]) + '-' + String(pos[1][0]) + ':' +
            String(pos[1][1])]);
        }
      }
      return att;
    }

    const parsed = reader.parse(markdown);
    const walker = parsed.walker();
    let event;

    while ((event = walker.next())) {
      let node = event.node;

      if (!event.entering) continue;

      if (node.type === 'heading') {
        const content = this.getText(writer.render(node));
        let id = '';
        const pos = node.sourcepos;
        if (pos) {
          id = String(pos[0][0]) + ':' + String(pos[0][1]) + '-' +
            String(pos[1][0]) + ':' + String(pos[1][1]);
        }
        this.headList.push({ level: node.level, content, anchor: id });
      }
      // if (node.type === 'text') {
      //   node.literal = node.literal.toUpperCase();
      // }
    }


    const result = writer.render(parsed);

    return result;
  }

  /**
   * Compile HTML: loop the head(h1-h6) of html to build nav.
   * @param {string} html
   * @returns {string}
   */
  compileHtml(html: string) {
    const div: HTMLElement = document.createElement('DIV');
    div.innerHTML = html;

    const headList: Array<Head> = [];

    const loopChild = (nodeList: HTMLCollection) => {
      for (const node of Array.from(nodeList) as Array<HTMLElement>) {
        const headMatch = node.nodeName.match(/^H([1-6])$/i);
        if (headMatch) {
          headList.push({ level: Number(headMatch[1]), content: node.innerText, anchor: node.id });
          continue;
        }

        if (node.children.length) {
          loopChild(node.children);
          continue;
        }
      }
    }

    loopChild(div.children);
    this.headList = headList;

    return html;
  }

  pushEventEmit(value?: any) {
    this.eventEmit.emit(value);
  }

  private getText(text: string) {
    const div = document.createElement('DIV');

    div.innerHTML = text;

    return div.innerText;
  }

  private log(message) {
    // console.log(message);
  }

  private handleError<T>(operation = 'operation', result?: T) {
    return (error: any): Observable<T> => {
      this.log(`${operation} failed: ${error.message}`)

      return of(result as T);
    }
  }
}
