import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';

import {
  ElementRef,
  ViewChild,
  TemplateRef,
} from '@angular/core';
import { Observable } from 'rxjs';

import { ArticlesService } from '../services/articles/articles.service';
import { CommonMarkService } from '../common-mark/common-mark.service';

@Component({
  selector: 'app-articles',
  templateUrl: './articles.component.html',
  styleUrls: ['./articles.component.scss']
})
export class ArticlesComponent implements OnInit {
  artContent: Observable<any>
  tocContent: Array<any> = [];
  article: string;

  @ViewChild(TemplateRef, { read: false, static: true }) template: TemplateRef<any>;
  @ViewChild('panel', { read: false, static: true }) panel: ElementRef;

  constructor(
    private articlesService: ArticlesService,
    private route: ActivatedRoute,
    private commonMarkService: CommonMarkService
  ) { }

  ngOnInit() {
    const id = this.route.snapshot.paramMap.get('id');
    // this.articlesService.getArticle(id).subscribe((res: any) => this.article = res.data.content);

    this.artContent = this.articlesService.getArticle(id);

    this.commonMarkService.eventEmit.subscribe(value => {
      this.tocContent = this.commonMarkService.headList;
    });
  }

  onAnchorClick(anchor: string) {
    window.location.hash = '';
    window.location.hash = anchor;

    const body = document.documentElement;
    const scrollTop = body.scrollTop;
    const offsetHeight = body.offsetHeight;
    const scrollHeight = body.scrollHeight
    const maxScroll = scrollHeight - offsetHeight;

    if (scrollTop < maxScroll) {
      body.scrollTop = scrollTop - 52;
    }
  }
}
