import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { QueryFn } from '@angular/fire/firestore';

import { ArticlesService } from '../services/articles/articles.service';

@Component({
  selector: 'app-archives',
  templateUrl: './archives.component.html',
  styleUrls: ['./archives.component.scss']
})
export class ArchivesComponent implements OnInit {
  public archiveList: Array<API.Archive>;

  constructor(
    private articlesService: ArticlesService,
    private route: ActivatedRoute
  ) { }

  ngOnInit() {
    // Get Query String Parameters date.
    const date: string = this.route.snapshot.paramMap.get('date');
    const queryList = this.parseQueryList(date);

    this.getArchiveList(queryList);
  }

  /**
   * Parse Query List to ref.where() params
   * @param {string} date
   * @returns {[string, string, number | string][]}
   */
  parseQueryList(date: string): [string, string, number | string][] {
    let queryList: [string, string, number | string][];

    if (date && date.match(/^20\d{2}$/)) {
      queryList = [];
      queryList.push(['createTime', '>=', new Date(`${date}-01-01 00:00`).valueOf()]);
      queryList.push(['createTime', '<', new Date(`${Number(date) + 1}-01-01 00:00`).valueOf()]);
    }
    return queryList;
  }

  /**
   * Get Archive List
   * @param {QueryFn} queryList
   */
  getArchiveList(queryList?: [string, string, number | string][]) {
    let queryFn: QueryFn;

    if (queryList) {
      queryFn = ref =>
        queryList.reduce((previousValue, currentValue) =>
        previousValue.where.apply(previousValue, currentValue), ref)
    }

    this.articlesService.getArchives(queryFn).subscribe((data: Array<API.Article>) => {
      const archiveMap = {};

      data.sort(({ createTime: a }, { createTime: b }) => b - a);

      for (const item of data) {
        let fullYear = new Date(item.createTime).getFullYear();
        if (!archiveMap[fullYear]) archiveMap[fullYear] = [];
        archiveMap[fullYear].push(item);
      }

      this.archiveList = Object.keys(archiveMap).sort((a, b) => b.localeCompare(a)).map(key => ({ fullYear: key, articles: archiveMap[key] }));
    });
  }
}
