import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';

import { Observable } from 'rxjs';
import { map, tap } from 'rxjs/operators';
import { AngularFirestore, QueryFn } from '@angular/fire/firestore';
import { AngularFireAuth } from '@angular/fire/auth';
import firebase from 'firebase';

@Injectable({
  providedIn: 'root'
})
export class ArticlesService {

  constructor(
    private http: HttpClient,
    public db: AngularFirestore,
    private angularFireAuth: AngularFireAuth
  ) { }

  /**
   * Get User info
   * @returns {firebase.User}
   */
  get user(): Observable<firebase.User> {
    return this.angularFireAuth.user;
  }

  /**
   * return firebase.auth.Auth
   * @returns { firebase.auth.Auth }
   */
  get auth() {
    return this.angularFireAuth;
  }

  /**
   * Get article list
   */
  getArticleList(queryFn?: QueryFn): Observable<API.Article[]> {
    return this.db.collection<API.Article>('article', queryFn).snapshotChanges().pipe(
      map(actions => actions.map(a => {
        const data = a.payload.doc.data();
        const id = a.payload.doc.id;

        return { ...data, id };
      }))
    );
  }

  /**
   * get article content by article id
   * @param id
   * @returns {Observable<API.Article>}
   */
  getArticle(id: number|string): Observable<API.Article> {
    // return this.http.get(`/api/articleLists/${id}`).pipe(
    //   tap(_ => console.log(`fetched article id=${id}`))
    // );
    return this.db.doc<API.Article>(`article/${id}`).valueChanges();
  }

  /**
   * Get Archives
   * @param {QueryFn} queryFn 查询条件回调
   * @returns {Observable<API.Article[]>}
   */
  getArchives(queryFn?: QueryFn): Observable<API.Article[]> {
    return this.db.collection<API.Article>('article', queryFn)
      .snapshotChanges()
      .pipe(map(actions => actions.map(a => ({ ...a.payload.doc.data(), id: a.payload.doc.id }))));
  }

  /**
   * Get Category list
   */
  getCategorys() {
    return this.db.collectionGroup<API.Tagmark>('tagmarks', ref => ref.where('type', '==', 'category'))
      .snapshotChanges()
      .pipe(map(actions => actions.map(a => ({ ...a.payload.doc.data(), id: a.payload.doc.id }))));
  }

  /**
   * Get Tag list
   */
  getTags() {
    return this.db.collectionGroup<API.Tagmark>('tagmarks', ref => ref.where('type', '==', 'tag'))
      .snapshotChanges()
      .pipe(map(actions => actions.map(a => ({ ...a.payload.doc.data(), id: a.payload.doc.id }))));
  }

  async addArticle(article: API.Article): Promise<firebase.firestore.DocumentReference<API.Article | API.Tagmark> | void> {
    const id = article.id;
    // If the article has id, to update.
    if (id) {
      delete article.id;
      return this.updateArticle(id, article);
    }

    article.createTime = new Date().valueOf();

    const result = this.db.collection<API.Article>('article').add(article);

    const articleId = result[0].id;

    const tagmarksCollection = this.db.collection<API.Article>('article').doc(articleId).collection<Partial<API.Tagmark>>('tagmarks');

    // Add tagmarks category
    await tagmarksCollection.add({ value: article.category, type: 'category' });
    // Add tagmarks tag
    for (const item of article.tags) {
      await tagmarksCollection.add({ value: item, type: 'tag' });
    }
    return result;
  }

  async updateArticle(id: string, article: Partial<API.Article>): Promise<void> {
    article.updateTime = new Date().valueOf();
    const result = await this.db.collection<Partial<API.Article>>('article').doc(id).set(article, { merge: true });

    const tagmarksCollection = this.db.collection<API.Article>('article').doc(id).collection<Partial<API.Tagmark>>('tagmarks');

    // 获取一遍 tagmarks 下的所有文档，并对文档进行删除
    tagmarksCollection.get().subscribe(async (querySnapshot) => {
      for (const doc of querySnapshot.docs) {
        await doc.ref.delete();
      }

      // Add tagmarks category
      await tagmarksCollection.add({ value: article.category, type: 'category' });
      // Add tagmarks tag
      for (const item of article.tags) {
        await tagmarksCollection.add({ value: item, type: 'tag' });
      }
    });

    return result;
  }

  /* 后面三个数据调用接口的方法是调用自定义的后台服务的接口 */

  // get article list
  getLists() {
    return this.http.get('/api/articleLists');
  }

  getMdContent(url: string): Observable<any> {
    return this.http.post('/api/mdContent', { "contentUrl": url, "id": 1 }).pipe(
      tap(_ => console.log(`fetched article content url=${url}`))
    )
  }

  addArticleToService(content: object): Observable<any> {
    return this.http.post('/api/demo/insert', content).pipe(
      tap(_ => console.log(`fetched article content=${JSON.stringify(content)}`))
    )
  }
}
