import {
  Component,
  Input,
  OnChanges,
  AfterViewInit,
  ElementRef,
} from '@angular/core';

import { CommonMarkService } from './common-mark.service';

@Component({
  selector: 'common-mark',
  templateUrl: './common-mark.component.html',
  styleUrls: ['./common-mark.component.scss']
})
export class CommonmarkComponent implements OnChanges, AfterViewInit {

  public content: string = '';

  @Input() data: string;
  @Input() src: string;
  @Input() type: string;

  constructor(
    private commonMarkService: CommonMarkService,
    private element: ElementRef<HTMLElement>,
  ) { }

  ngOnChanges() {
    if (this.data) {
      this.handleData();
      return null;
    }

    if (this.src) {
      this.handleSrc();
      return null;
    }
  }

  ngAfterViewInit() {
    if (this.data || this.src) return null;

    this.handleInclude();
  }

  // @Input data have content
  handleData() {
    this.render(this.data);
  }

  // @Input src have content, to get markdown source.
  handleSrc() {
    this.commonMarkService.getSource(this.src).subscribe((markdown: string) => {
      this.render(markdown);
    })
  }

  // included in the <common-mark> tag.
  handleInclude() {
    // console.log(this.element.nativeElement.innerHTML)
    this.render(this.element.nativeElement.innerHTML);
  }

  // compile markdown to html and render.
  render(markdown: string) {
    let compiled: string;
    if (this.type === 'html') {
      this.commonMarkService.compileHtml(markdown);
      compiled = markdown;
    } else {
      compiled = this.commonMarkService.compile(markdown);
    }

    this.commonMarkService.pushEventEmit();
    this.element.nativeElement.innerHTML = compiled;
  }

}
